import { Component } from '@angular/core';
import { ModalController } from 'ionic-angular';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the DataPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-data',
  templateUrl: 'data.html',
})
export class DataPage {

  responden: any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private service: ServiceProvider,
    private modalCtrl: ModalController
    ) {

      console.log('admin', this.navParams.get('admin'))
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DataPage');
  }

  ionViewWillEnter() {
    if(!this.navParams.get('admin')) {
      this.navCtrl.setRoot('BerandaPage')
    }

    if(this.navParams.get('admin')) {
      this.service.LOADER()
      this.service.ambilDataResponden().then((res) => {
        console.log(res)
        this.responden = res;
        this.service.Loader.dismiss();
      }, err => {
        this.service.Loader.dismiss();
      })
    } 
    
  }

  itemSelected(item) {
    console.log(item)
    // this.service.saveData('Responden', item).then((ok) => {
    //   console.log(ok)
    // })
    const modal = this.modalCtrl.create("DetailRespondenPage", {data: item});
    modal.present();
  }

}



